.pacman-wrapper {
  position: relative;
  font-size: 14px;
  overflow: hidden;
  width: 300px;
  height: 350px;
}

#pacman-labirint {
  position: relative;
  width: 21.4285714286em;
  height: 25.1428571429em;
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}

#pacman-labirint > svg {
  width: 100%;
  height: 100%;
}

#pacman-labirint #borders path {
  stroke-dashoffset: 400;
  stroke-dasharray: 400;
  transition: stroke-dashoffset 1s linear;
}

#pacman-labirint #main-line {
  stroke-dashoffset: 2920;
  stroke-dasharray: 2920;
}

#pacman-labirint .red-ghost,
#pacman-labirint .green-ghost,
#pacman-labirint .blue-ghost,
#pacman-labirint #dots {
  opacity: 0;
}

#pacman-labirint .red-ghost,
#pacman-labirint .green-ghost,
#pacman-labirint .blue-ghost {
  position: absolute;
  z-index: 1;
}

#pacman-labirint .red-ghost {
  top: 3.9285714286em;
  left: 11.1428571429em;
}

#pacman-labirint .green-ghost {
  top: 6.1428571429em;
  left: 19.7857142857em;
}

#pacman-labirint .blue-ghost {
  top: 10.3571428571em;
  left: 2.6428571429em;
}

#pacman-labirint .pacman {
  position: absolute;
  opacity: 0;
  top: -0.1428571429em;
  left: 0.5em;
  z-index: 1;
  transform-origin: 50% 50%;
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
  width: 1.1428571429em;
  height: 1.1428571429em;
}

#pacman-labirint .pacman:before, #pacman-labirint .pacman:after {
  content: "";
  position: absolute;
  z-index: 1;
  left: 0;
  width: 100%;
  height: 50%;
  background: #FFB03B;
  overflow: hidden;
}

#pacman-labirint .pacman:before {
  top: 0;
  border-radius: 1.1428571429em 1.1428571429em 0 0;
  transform-origin: center bottom;
}

#pacman-labirint .pacman:after {
  bottom: 0;
  border-radius: 0 0 1.1428571429em 1.1428571429em;
  transform-origin: center top;
}

#pacman-labirint.show-pacman #borders path {
  stroke-dashoffset: 0;
}

#pacman-labirint.show-pacman .red-ghost,
#pacman-labirint.show-pacman .green-ghost,
#pacman-labirint.show-pacman .blue-ghost,
#pacman-labirint.show-pacman #dots {
  opacity: 1;
  transition: opacity 1s linear 1s;
}

#pacman-labirint.show-pacman #main-line {
  -webkit-animation: hide-dots 30s linear 1s;
  animation: hide-dots 30s linear 1s;
}

#pacman-labirint.show-pacman .pacman {
  opacity: 1;
  transition: opacity 1s linear 1s;
  -webkit-animation: pacman-move 30s linear 1s;
  animation: pacman-move 30s linear 1s;
}

#pacman-labirint.show-pacman .pacman:before {
  -webkit-animation: pacman-top 0.4s ease infinite 1s;
  animation: pacman-top 0.4s ease infinite 1s;
}

#pacman-labirint.show-pacman .pacman:after {
  -webkit-animation: pacman-bottom 0.4s ease infinite 1s;
  animation: pacman-bottom 0.4s ease infinite 1s;
}

#pacman-labirint.show-pacman .red-ghost {
  -webkit-animation: red-ghost 1.8947368421s linear infinite 0.3684210526s;
  animation: red-ghost 1.8947368421s linear infinite 0.3684210526s;
}

#pacman-labirint.show-pacman .blue-ghost {
  -webkit-animation: blue-ghost 2.5263157895s linear infinite 0.6842105263s;
  animation: blue-ghost 2.5263157895s linear infinite 0.6842105263s;
}

#pacman-labirint.show-pacman .green-ghost {
  -webkit-animation: green-ghost 1.8947368421s linear infinite 1.3157894737s;
  animation: green-ghost 1.8947368421s linear infinite 1.3157894737s;
}

@-webkit-keyframes hide-dots {
  0% {
    stroke-dashoffset: 2920;
  }
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes hide-dots {
  0% {
    stroke-dashoffset: 2920;
  }
