  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  font-size: inherit;
  padding: 0;
  font-family: var(--font-family);
  width: 12rem;
  height: auto; }
